<template>
  <div class="changePwd">
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>您现在的位置：服务管理</el-breadcrumb-item>
        <el-breadcrumb-item>个性设置</el-breadcrumb-item>
        <el-breadcrumb-item>修改密码</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div style="padding: 20px 0">
      <el-form ref="addForm" :model="addForm" :rules="rule" label-width="200px" style="width: 600px">
        <el-form-item label="选择密码种类：" prop="userFul">
          <el-select v-model="addForm.userFul" style="width:260px;">
            <el-option label="登录密码" value="loginPwd"></el-option>
            <el-option label="交易密码" value="trainPwd"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="请输入原密码：" prop="oldPwd">
          <el-input v-model="addForm.oldPwd" type="password" style="width: 260px"></el-input>
        </el-form-item>

        <el-form-item label="请输入新密码：" prop="newPwd">
          <el-input v-model="addForm.newPwd" type="password" style="width: 260px"></el-input>
        </el-form-item>

        <el-form-item label="请再次输入新密码：" prop="againNewPwd">
          <el-input v-model="addForm.againNewPwd" type="password" style="width: 260px"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button
            size="mini"
            type="primary"
            style="background-color: #1f93d5;border-color: #1f93d5;"
            @click="changePwd"
          >确定</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'changePwd',
  data () {
    return {
      addForm: {
        oldPwd: '',
        newPwd: '',
        againNewPwd: '',
        userFul: ''
      },
      rule: {
        oldPwd: [{ required: true, message: '请输入原密码', trigger: 'blur' }],
        newPwd: [{ required: true, message: '请输入新密码', trigger: 'blur' }],
        againNewPwd: [{ required: true, message: '请再次输入新密码', trigger: 'blur' }],
        userFul: [{ required: true, message: '请选择密码种类', trigger: 'change' }]
      }
    }
  },
  created () {

  },
  methods: {
    changePwd () {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          if (this.addForm.oldPwd === this.$store.state.userInfo.password) {
            if (this.addForm.newPwd === this.addForm.againNewPwd) {
              this.$store.state.userInfo.password = this.addForm.newPwd
              this.$message.success('修改密码成功')
              this.addForm.oldPwd = ''
              this.addForm.newPwd = ''
              this.addForm.againNewPwd = ''
            } else {
              this.$message.error('两次输入的新密码不一致')
            }
          } else {
            this.$message.error('原密码不正确')
          }
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.changePwd {
  .header {
    padding: 20px 10px;
    font-size: 14px;
     /deep/ .el-breadcrumb__inner{
      font-size: 16px;
    }
  }
}
</style>
